import { AppProps } from 'next/app';

import {
  DevicePreferencesProvider,
  FeatureFlagProvider,
  ThemeProvider,
} from '@cbhq/cds-web/system';
import '@cbhq/cds-web/globalStyles';
import { identify, init, PlatformName } from '@cbhq/client-analytics';
import { useEffect } from 'react';

export default function StaticApp({ Component, pageProps }: AppProps) {
  useEffect(() => {
    init({
      isProd: false,
      platform: PlatformName.extension,
      projectName: 'web-push-test',
      showDebugLogging: process.env.NODE_ENV !== 'production',
      version: '1.0.0',
    });
    identify({ userId: 'foo' });
  });

  return (
    <ThemeProvider>
      <FeatureFlagProvider>
        <DevicePreferencesProvider>
          <Component {...pageProps} />
        </DevicePreferencesProvider>
      </FeatureFlagProvider>
    </ThemeProvider>
  );
}
